import React from 'react';

const Parameters = () => {
    return (<div className="parameters">
        <h2 className="parameters--title">Parameters</h2>
        <p className="parameters--description">Some sensors provided by the API output more information, some less. The main three values that are predominant in most measurements are PM2.5, PM10 and PM1.</p>
        <p className="parameters--description">PM1 is the most toxic. Due to its size it may percolate dirrectly to the bloodstream of the person inhaling it. PM2.5 is slightly bigger, and as such gets stuck in the alveoli of human (and non-human) lungs. PM10 is the most common one and stays inside the lungs, without entering the alveoli or bloodstream dirrectly. </p>
        <p className="parameters--description">The search engine will provide you with the nearest station's sensor output. Be aware, that it is sensitive to diacritic signs ("ń", "ą" and others) - if you search for Poznan instead of Poznań - it will still find your destination, but the search engine might get confused sometimes.</p>
    </div>);
}

export default Parameters;
