import React, { useState, useEffect } from 'react';
import Search from './Search'
import Header from './Header'
import Parameters from './Parameters'
import City from './City';
import SearchFail from './SearchFail'
import Spinner from './Spinner'
import '../style.scss'

const App = () => {

  const [searched, setSearched] = useState("")
  const [city, setCity] = useState("");
  const [airState, setAirState] = useState("")
  const [sensorPlace, setSensorPlace] = useState("")
  const [measuredValues, setMeasuredValues] = useState([])
  const [searchValidated, setSearchValidated] = useState(false)
  const [loading, setLoading] = useState(null)

  const airlyApi = "0QMeVpdC5bNVH7xwQlkEOZJ9FYBB5NCa"

  useEffect(() => {
    fetch(' http://ip-api.com/json/').then(res => {
      if (!res.ok) { throw res; }
      res.json()
    }).then(resolved => { search(null, resolved.city) }).catch(err => console.log(err))
  }, [])


  const search = (e, searchValue) => {
    if (e) {
      e.preventDefault();
    }
    if (searchValue.length > 2 & searchValue !== "") {
      setLoading(true)
      fetch(`https://nominatim.openstreetmap.org/search?q=${searchValue}&format=json&limit=1&namedetails=1`).then(res => res.json()).then(response => {
        if (response[0]) {
          setSearchValidated(true)
          getMeasurements(Number(response[0].lat).toFixed(2), Number(response[0].lon).toFixed(2));
          setSearched(searchValue.toLowerCase())
        }
        else {
          setLoading(false)
          setSearchValidated(false)
        }
      })
    }
  }
  const getMeasurements = (lat, lng) => {
    if (lat > 49.04 && lat < 54.84 && lng > 14.12 && lng < 24.15) {
      searchSuccess(lat, lng)
    }
    else {
      setTimeout(() => { setLoading(false) }, 1000);
      setSearchValidated(false)
    }
  }
  const searchSuccess = (lat, lng) => {

    let apiHeaders = {
      Accept: "application/json",
      Apikey: `${airlyApi}`, "Accept-Language": "en"
    }
    let measurements = fetch(`https://airapi.airly.eu/v2/measurements/nearest?lat=${lat}&lng=${lng}&maxResults=3&maxDistanceKM=100`, { headers: apiHeaders })
    let places = fetch(`https://airapi.airly.eu/v2/installations/nearest?lat=${lat}&lng=${lng}&maxResults=3&maxDistanceKM=100`, { headers: apiHeaders })


    Promise.all([measurements, places]).then(values => Promise.all(values.map(value => value.json()))).then(extractedValues => {
      setCity(extractedValues[1][0].address.city.toLowerCase()); setAirState(extractedValues[0].current.indexes[0].description); setSensorPlace(extractedValues[1][0].address.street ? extractedValues[1][0].address.street : "a not specified"); setMeasuredValues(extractedValues[0].current.values); setTimeout(() => { setLoading(false) }, 1000);
    })

  }

  return (
    <div className="App">
      <div className="App__info">
        <Header />
        <Parameters />
      </div>
      <div className="App__results">
        <Search search={search} />
        {loading ? <Spinner /> : searchValidated ? (<City searched={searched} city={city} airState={airState} sensorPlace={sensorPlace} measuredValues={measuredValues} />) : (<SearchFail />)}
      </div>
    </div>
  );
}

export default App;
