import React from 'react';


const pollutionColor = (name, value) => {

    if (name === "PM25" && value < 25) {
        return { background: "linear-gradient(to right, #1e960080 80%, #fff20080, #ff000080)" }
    }
    if (name === "PM25" && (value >= 25 && value <= 40)) {
        return { background: "linear-gradient(to right, #1e960080 20%, #fff20080 80%, #ff000080 )" }
    }
    if (name === "PM25" && value > 40) {
        return { background: "linear-gradient(to right, #1e960080, #fff20080, #ff000080 20%)" }
    }
    if (name === "PM10" && value < 50) {
        return { background: "linear-gradient(to right, #1e960080 80%, #fff20080, #ff000080)" }
    }
    if (name === "PM10" && (value >= 50 && value <= 80)) {
        return { background: "linear-gradient(to right, #1e960080 20%, #fff20080 80%, #ff000080)" }
    }
    if (name === "PM10" && value > 80) {
        return { background: "linear-gradient(to right, #1e960080, #fff20080, #ff000080 20%)" }
    }

}

const City = (props) => {

    // <span className="capitalize"> </span>

    return (
        <div className="fetchedInfo">
            {props.searched === props.city ? <h3 className="fetchedInfo--title"> Sensors in <span className="fetchedInfo--capitalize">{props.searched}</span> found! </h3> : <h3 className="fetchedInfo--title">No sensors in the exact location (<span className="fetchedInfo--capitalize">{props.searched}</span>)</h3>}
            {props.searched === props.city ? <p className="fetchedInfo--basic">We have sensors in <span className="fetchedInfo--capitalize">{props.city}</span>. {props.airState}</p> : <p className="fetchedInfo--basic"><span className="fetchedInfo--reminder"> The nearest sensor we found is in <span className="fetchedInfo--capitalize">{props.city}</span>.</span> {props.airState} </p>}
            <p className="fetchedInfo--basic"> The measurements for <span className="fetchedInfo--capitalize">{props.city}</span> you'll find below are based on what we analyzed on <span className="fetchedInfo--capitalize"> {props.sensorPlace} </span> street, but there might be other sensors over at <span className="fetchedInfo--capitalize">{props.city}</span>. If you want to try, you can add a street name to the search.</p>
            <h4 className="fetchedInfo--subtitle">Here is what we got:</h4>
            <ul className="fetchedInfo--measured">
                {props.measuredValues.length > 0 ? props.measuredValues.map(measurment => {
                    return (
                        <li className="fetchedInfo--specific" style={pollutionColor(measurment.name, measurment.value)} key={measurment.name}>
                            {measurment.name} : {measurment.value}
                        </li>)
                }) : <li>Strange, but it seams sensors for <span className="fetchedInfo--capitalize">{props.searched} </span>  are currently unavailable. Sorry for the inconveniance. </li>}
            </ul>
            <p className="fetchedInfo--basic">
                The WHO norms for PM2.5 is 25 μg/m3, for PM10 50 μg/m3. Ultrafine particles (PM1) are still under investigation, as the norm is hard to identify.
            </p>
        </div>
    );
}

export default City;