import React from 'react';
import spinner from '../img/Loader.svg'


const Spinner = () => {
    return (
        <div className="preloader">
            <h2 className="preloader--title">Breathing in the data</h2>
            <img className="preloader--spinner" src={spinner} alt="preloader spinner"></img>
        </div >);
}

export default Spinner;