import React, { useState } from 'react';




const Search = (props) => {

    const [searchValue, setSearchValue] = useState("")
    const currentInput = (e) => { setSearchValue(e.target.value) }
    const resetInput = () => { setSearchValue("") }

    return (
        <form className="search">
            <input className="search--input"
                placeholder="Search city"
                minLength="2"
                maxLength="40"
                value={searchValue}
                onChange={currentInput}
                type="text" />
            <input className="search--btn" onClick={(e) => { props.search(e, searchValue); resetInput() }} type="submit" value="Take a Breath!" />
        </form>
    );
}

export default Search;