import React from 'react';
import airlyLogo from '../img/AirlyBlue.png'

const Header = () => {
    return (
        <header className="header">
            <h1 className="header--title" >Take a Breath!</h1>
            <p className="header--description">This app is meant to give you an idea about air condition in different polish cities. It operates thanks to three different APIs listed below.</p>
            <ol className="header--list">
                <li className="header--listItem">Airly for gathering pollution indexes <a className="header--link" target="blank" href="https://airly.eu"> (read more) <img className="header--airly" src={airlyLogo} alt="airly logo" /></a> </li>
                <li className="header--listItem">Nominatim for gathering geolocation information <a className="header--link" target="blank" href="https://nominatim.org">(read more)</a> </li>
                <li className="header--listItem">IP-API for getting the initial state of the app with your current location <a className="header--link" target="blank" href="http://ip-api.com">(read more)</a>. The web is quite a complicated place, so sometimes this API will mistake your current location with something rather odd.</li>
            </ol>
        </header>
    );
}

export default Header;